.doctor-card {
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    overflow: hidden;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    cursor: pointer;
    margin: 16px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.doctor-card:hover {
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
    transform: translateY(-4px);
}

.doctor-card-header {
    background-color: #0d9780;
    color: white;
    padding: 16px;
    font-size: 20px;
    font-weight: bold;
}

.doctor-card-body {
    padding: 16px;
}

.doctor-card-body p {
    margin: 8px 0;
}

.doctor-card-body p b {
    color: #555;
}
